import React from 'react';
import './App.css';
import ViewApp from './Components/ViewApp/ViewApp';
import rp from './img/rp.png';
import instaLogo from './insta.jfif';
import teleLogo from './telega.jfif';
import ScrollUpBtn from './Components/ScrollUpBtn/ScrollUpBtn';

function App() {
  const apps = [
    {
      name: 'RandomPhotos',
      ref: '/randomphotos',
      img: rp,
      desc: 'Actually project was created just for demonstration of how works searching. You can search authors of random photos',
    },
    {
      name: 'RandomPhotos',
      ref: '/randomphotos',
      img: rp,
      desc: 'Actually project was created just for demonstration of how works searching. You can search authors of random photos',
    },
  ];

  const conDivStyle = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-around',
    width: '15%',
    textDecoration: 'none',
    color: '#000',
  };

  return (
    <>
      <div className="sub_html">
        <div>
          <ScrollUpBtn
            StopPosition={0}
            ShowAtPosition={150}
            EasingType="easeIn"
            AnimationDuration={1}
            ContainerClassName="ScrollUpButton__Container"
            TransitionClassName="ScrollUpButton__Toggled"
            style={{}}
            ToggledStyle={{}}
          />
        </div>
        <div id="bg_content">
          <div className="header">
            <div className="main_header_title">My Portfolio</div>
            <div className="header_navbar">
              <ul>
                <li>
                  <a href="#projects">Projects</a>
                </li>
                <li>
                  <a href="#contacts">Contacts</a>
                </li>
              </ul>
            </div>
          </div>
          <div id="header_main_content">
            <div className="header_content">
              <div className="header_title">
                <div>Hey, I'm Ainizoda</div>
                <div className="subtitle">Front-end developer</div>
              </div>
              <div className="header_desc">
                I'm 17 years old. I like making <strong>websites</strong> and
                programming in general. I am inspired by this work and get
                pleasure from the
                <strong> process of creation</strong>. In this site you will
                find some examples from my work.
              </div>
            </div>
          </div>
        </div>
        <div className="sub_body" id="projects">
          <div className="AppViews">
            {apps.map((i, ind) => (
              <ViewApp key={ind} data={i} right={ind % 2 ? true : false} />
            ))}
          </div>
        </div>
        <div id="contacts" className="sub_body">
          <h1>Contacts</h1>
          <div
            style={{ display: 'flex', justifyContent: 'space-around' }}
            className="sub_body"
            d
          >
            <div>
              <a style={conDivStyle} href="https://www.instagram.com/calmmec">
                <img
                  style={{ width: 20, height: 20 }}
                  src={instaLogo}
                  alt="insta logo"
                />
                <h2>Instagram</h2>
              </a>
            </div>
            <div>
              <a style={conDivStyle} href="https://www.t.me/calmmec">
                <img
                  style={{ width: 20, height: 20 }}
                  src={teleLogo}
                  alt="insta logo"
                />
                <h2>Telegram</h2>
              </a>
            </div>
          </div>
        </div>
      </div>

      <div className="nostyle">
        <h2>Sorry, this content is not available in mobile version :-(</h2>
      </div>
    </>
  );
}

export default App;
