import React from "react";
import "./Card.css";

function Card({ data: o, input }) {
  const markFound = (text) => {
    const found = text.trim().toLowerCase().indexOf(input.toLowerCase().trim());
    return (
      <>
        {found > -1 ? text.slice(0, found) : ''}
        <mark>{text.slice(found, found+input.length)}</mark>
        {text.slice(found+input.length, text.length)}
      </>
    );
  };
  
  return (
    <div className="card" key={o.id}>
      <img src={o.download_url} alt={o.id}></img>
      <div className="info">
        <h3>{input.length ? markFound(o.author) : o.author}</h3>
        <div>
          <small>{o.url}</small>
        </div>
      </div>
    </div>
  );
}

export default Card;
