import React, { useEffect, useState } from 'react';
import './Home.css';
import Card from '../Card/Card';
import SearchForm from '../SearchForm/SearchForm';
import loading from '../../loading.jpg';

function Home() {
  const [items, setItems] = useState([]);
  const [fieldLength, setFieldLength] = useState(0);
  const [input, setInput] = useState('');

  useEffect(() => {
    fetchItems();
  }, []);

  const fetchItems = async () => {
    const data = await fetch('https://picsum.photos/v2/list?page=2&limit=50');
    const items = await data.json();
    setItems(items);
    return items;
  };

  const handleSearch = async (fieldValue) => {
    const foundItems = await fetchItems();
    setItems(
      foundItems.filter(
        (o) =>
          o.author
            .toLowerCase()
            .trim()
            .indexOf(fieldValue.toLowerCase().trim()) !== -1
      )
    );
    setFieldLength(fieldValue.length);
    setInput(fieldValue);
  };

  return (
    <>
      <SearchForm onSearch={handleSearch} />
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <div className="cards">
          {items.length ? (
            items.map((o) => <Card key={o.id} data={o} input={input} />)
          ) : fieldLength ? (
            <h2>No items found</h2>
          ) : (
            <img src={loading} className="loading_img" alt="Loading..." />
          )}
        </div>
      </div>
    </>
  );
}

export default Home;
