import React from 'react';
import { Link } from 'react-router-dom';
import './ViewApp.css';

function ViewApp({ data, right }) {
  const { name, ref, img, desc } = data;

  const image = <img src={img} alt={name} />;

  const info = (
    <div className="info">
      <div className="appName">
        <Link to={ref} style={{ color: 'inherit', textDecoration: 'none' }}>
          {name}
        </Link>
      </div>
      <div className="description">{desc}</div>
    </div>
  );

  return (
    <div className="item">
      {right ? info : image}
      {right ? image : info}
    </div>
  );
}

export default ViewApp;
