import React from "react";
import { Link } from "react-router-dom";
import "./Header.css";

function Header() {
  return (
    <header>
      <div className="title">
        <span>random photos</span>
      </div>
      <ul>
        <li>
          <Link to="/randomphotos">Home</Link>
        </li>
        <li>
          <Link to="/randomphotos/about">About</Link>
        </li>
      </ul>
    </header>
  );
}

export default Header;
